import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { motion } from "framer-motion";
import { setData } from './slice/weatcherSlice';

const API_KEY = 'e87e712ff0294ae79f703cbf6539fa83';
const APIKEY = '0c1ec3857e4df4cfe89d49b5c813757f';

export default function Searchbar() {
    const [onFocus, setOnFocus] = useState(false);
    const [searchTerm, setSearchTerm] = useState('');
    const [suggestions, setSuggestions] = useState([]);
    const [selectedSuggestion, setSelectedSuggestion] = useState(null);
    const [geolocation, setGeolocation] = useState(null);
    const [isCurrent, setIsCurrent] = useState(false);
    const dispatch = useDispatch();

    const handleInputChange = (e) => {
        const value = e.target.value;
        setSearchTerm(value);
        fetch(`https://api.geoapify.com/v1/geocode/autocomplete?text=${value}&apiKey=${API_KEY}`, { method: 'GET' })
            .then(response => response.json())
            .then(result => {
                const data = result.features;
                setSuggestions(data.map(datum => {
                    const { lat, lon, city, country, formatted } = datum.properties;
                    return { lat, lon, city, country, formatted };
                }));
            })
            .catch(error => console.log('error', error));
    };

    const getData = (value) => {
        if (value?.lat && value?.lon) {
            fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${value.lat}&lon=${value.lon}&units=metric&appid=${APIKEY}`)
                .then(response => response.json())
                .then(response => {
                    const { clouds, name, main, wind, weather } = response;
                    dispatch(setData({ clouds, name, main, wind, weather }));
                })
                .catch(e => { console.error(e); });
        } else {
            console.error('Invalid coordinates');
        }
    };

    const handlerGetInfoWeatcher = () => {
        if (selectedSuggestion) {
            getData(selectedSuggestion);
        }
    };

    const handleSuggestionClick = (suggestion) => {
        setSelectedSuggestion(suggestion);
        setSearchTerm(suggestion.formatted);
        setSuggestions([]);
    };

    const getGeolocation = () => {
        navigator.geolocation.getCurrentPosition(position => {
            setIsCurrent(true);
            setGeolocation({
                lat: position.coords.latitude,
                lon: position.coords.longitude,
            });
        }, error => {
            console.error('Geolocation error:', error);
        });
    };

    useEffect(() => {
        if (navigator.geolocation) {
            getGeolocation();
        }
    }, []);

    useEffect(() => {
        if (geolocation) {
            getData(geolocation);
        }
    }, [geolocation]);

    return (
        <div className="relative">
            <motion.button
                disabled={geolocation === null || isCurrent === true}
                initial={{ x: "-100vw" }}
                animate={{ x: 0, transition: { delay: 1, duration: 0.6 } }}
                whileTap={{ opacity: 0.7 }}
                whileHover={{ scale: 1.1 }}
                onClick={() => getGeolocation()}
                className="absolute flex justify-center bg-blue-600 top-[1vh] left-[1vw] text-xl px-auto border-0 rounded-xl shadow-md placeholder:text-xl placeholder:font-bold"
            >
                <img src="place.png" alt="..." className='h-16 w-16' />
            </motion.button>
            <form className='relative left-[5vw]' onSubmit={(e) => {
                e.preventDefault();
                handlerGetInfoWeatcher();
            }}>
                <motion.input
                    type="text"
                    name="search"
                    id="search"
                    placeholder='بحث عن بلد'
                    value={searchTerm}
                    onChange={handleInputChange}
                    initial={{ y: "-100vh" }}
                    animate={{ y: 0 }}
                    transition={{ delay: 1, duration: 0.6 }}
                    className={`w-[75vw] ml-5 mt-3 py-3 text-xl pl-3 border-2 placeholder:text-xl placeholder:font-bold ${onFocus && "outline-[#00d287]"} rounded-md`}
                    onFocus={() => setOnFocus(true)}
                    onBlur={() => {
                        setOnFocus(false);
                        setTimeout(() => setSuggestions([]), 200);
                    }}
                />
                <motion.button
                    initial={{ y: "-100vh" }}
                    animate={{ y: 0, transition: { delay: 1, duration: 0.6 } }}
                    whileHover={{ scale: 1.15, fontWeight: 700 }}
                    whileTap={{ opacity: 0.7 }}
                    className="relative left-[2vh] bg-[#00f156] px-14 py-4 mt-2 border-0 rounded-lg text-[#2f4858] text-xl font-bold"
                    onClick={(e) => {
                        e.preventDefault();
                        handlerGetInfoWeatcher();
                    }}
                >
                    بحث
                </motion.button>
            </form>
            {suggestions.length > 0 && (
                <ul className="absolute z-10 left-[5vw] w-[75vw] ml-5 bg-white border border-gray-300 rounded-md shadow-lg max-h-60 overflow-y-auto">
                    {suggestions.map((suggestion, index) => (
                        <li
                            key={index}
                            className="px-4 py-2 hover:bg-gray-100 cursor-pointer"
                            onClick={() => handleSuggestionClick(suggestion)}
                        >
                            {suggestion.formatted}
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
}
