import { createSlice } from "@reduxjs/toolkit";

const initialState={clouds:undefined,
name:undefined,
main:undefined,
wind:undefined,
weatcher:undefined,
};
export const weatcherSlice=createSlice(
 {name:"Weatcher",
    initialState,
    reducers:{
setData:(state,aciton)=>{
    const {clouds,name,main,wind,weather}=aciton.payload;
    state.clouds=clouds;
    state.name=name;
    state.main=main;
    state.wind=wind;
    state.weather=weather[0];

}
    }
 }   
);

export const {setData}=weatcherSlice.actions;
export default weatcherSlice.reducer;