import React from 'react';
import { useSelector } from 'react-redux';
import { motion } from 'framer-motion';
export default function Card() {
  const weatcher = useSelector(({ weatcher }) => weatcher);

  // Use optional chaining and default values
  const name = weatcher?.name || 'Unknown';
  const temp = weatcher?.main?.temp ? weatcher.main.temp : null; // Set temp to null if not available
  const speed = weatcher?.wind?.speed || 'N/A';
const iconWeather=weatcher?.weather?.icon||null;
  // Helper function to format temperature
  const formatTemp = (temp) => {
    return temp !== null ? temp.toFixed(2) : 'N/A';
  };
  return (
    <>
      {weatcher.name!=null ?(
        <motion.div initial={{y:'100vh'}} animate={{y:'0',transition:{delay:1,duration:0.7}}} className="container relative mx-auto mt-10 min-h-[50vh] flex flex-col items-stretch text-center border-0 rounded-2xl text-white" style={{ width: "40vw", backgroundColor: "rgba(255,255,255,0.4)" }}>
          <h1 className='font-semibold tracking-wide text-5xl mb-5 mt-2'   initial={{y:'100vh'}} animate={{y:'0',transition:{delay:1,duration:0.7}}}>{name}</h1>
          <motion.img
  src={`https://openweathermap.org/img/wn/${iconWeather}@2x.png`}
  alt="Weather Icon"
  className="h-64 w-64 mb-10 border-0 rounded-lg self-center"
  initial={{ x: '100vw', opacity: 0 }}
  animate={{ 
    x: [0, 20, -20, 0],
    y: [0, 20, -20, 0],
    opacity: 1
  }}
  transition={{
    duration: 7,
    ease: "easeInOut",
    repeat: Infinity,
    repeatType: "loop"
  }}
/>
          <span>
            <h3 className='inline text-4xl mt-5'>{formatTemp(temp)}°C</h3>
            <img  src="thermometer.png" alt="Thermometer" className='absolute h-[48px] top-[38vh] left-[23.5vw] bg'/>
          </span>

          <div className='container flex flex-row h-full w-[23vw] mt-10 mb-3 mx-auto p-4 border-0 rounded-xl bg-black opacity-40'>
            <div className="flex flex-col h-full mx-auto items-center">
              <img src="wind.png" alt="Wind Icon" />
              <h5>{speed}km/H</h5>
            </div>
            <hr className='bg-black rotate-90  h-3 w-[7vh] self-center' />
            <div className="flex flex-col h-full mx-auto items-center">
              <img src="thermometer2.png" alt="Thermometer Icon" />
              <h5>{formatTemp(temp)}°C</h5>
            </div>
            <hr className='bg-black rotate-90  h-3 w-[7vh] self-center' />
            <div className="flex flex-col h-full mx-auto items-center">
              <img src="humidity.png" alt="Humidity Icon" />
              <h5>{formatTemp(temp)}%</h5>
            </div>
          </div>
        </motion.div>
      ):<div className='w-[85vw] ml-5 mt-3 py-3 text-xl pl-3 mr-4 border-2 placeholder:text-xl placeholder:font-bold text-center tracking-widest rounded-md bg-white opacity-70'>please choose city or country</div>}
    </>
  );
}
